<template>
    <yd-layout>
        <div class="mainContent">
            <div class="contentSradius querySradius">
                <yd-cell-item>
                    <input slot="right" type="text" placeholder="请输入MCC码" v-model="mccCode">
                </yd-cell-item>
                <yd-cell-item>
                    <input slot="right" type="text" placeholder="请输入含义或名称" v-model="mccName">
                </yd-cell-item>
            </div>
            <yd-button v-on:click.native="mccQuery()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">搜索</yd-button>
            <div class="msg" v-if="seachNum == 1">温馨提示: 请输入搜索条件后点击搜索按钮进行搜索！</div>
        </div>
        <div class="mainContentFluid" v-if="seachNum == 2">
            <div class="fluidWidth mccCode" v-for="(item, index) in mccData" :key="index">
                <yd-flexbox>
                    <div class="rightTitle">MCC：</div>
                    <yd-flexbox-item v-text="item.MCC"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="type">类 别：</div>
                    <yd-flexbox-item v-text="item.TYPE"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="rightTitle">代码名称：</div>
                    <yd-flexbox-item v-text="item.CODENAME"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="codeMean">代码含义：</div>
                    <yd-flexbox-item v-text="item.CODEDETAIL"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="rightTitle">发卡方收益：</div>
                    <yd-flexbox-item v-text="item.CARDUSERSY"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="codeName">排 除：</div>
                    <yd-flexbox-item v-html="trim(item.EXCLUDE)||'&nbsp;'"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div class="codeName">网络服务费：</div>
                    <yd-flexbox-item v-html="trim(item.SERVICEFEE)||'&nbsp;'"></yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="mainContentFluid" v-if="seachNum==3">
            <div class="fluidWidth mccCode">
                <div class="noResults">无搜索结果</div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'mcc',
    data() {
        return {
            mccCode: '',
            mccName: '',
            mccData: [],
            seachNum: 1
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted() {
        document.setTitle('MCC码查询');
    },
    methods: {
        mccQuery() {
            var vm = this;
            if (vm.mccCode == '' && vm.mccName == '') {
                vm.$dialog.toast({
                    mes: '请输入MCC码、含义或名称',
                    timeout: 1000
                });
                return false;
            } else {
                vm.get(vm.conf.portalMobile + '/API/PortalMobile/menus/mcc', {
                    'name': vm.getUserInfo.ssotoken,
                    'MccCode': vm.mccCode,
                    'MeaningOrName': vm.mccName
                }, function (res) {
                    if (res.jsonData.length == 0) {
                        vm.seachNum = 3;
                    } else {
                        vm.seachNum = 2;
                        vm.mccData = res.jsonData;
                    }
                })
            }
        }
    }
}
</script>
<style scoped>
.mainContent {
    height: auto!important;
}

.msg {
    font-size: .24rem;
    color: #999999;
    text-align: center;
    margin-top: .31rem;
}

.msg:before {
    content: '';
    display: inline-block;
    height: .44rem;
    width: .44rem;
    vertical-align: middle;
    background: url(../../../static/images/query/q_1.png) no-repeat 0 .01rem;
    background-size: .4rem;
}

.mccCode {
    border-bottom: 5px solid #ececec;
}

.mccCode .yd-flexbox {
    font-size: 0.28rem;
    border-bottom: 1px solid #ccc;
    line-height: 0.8rem;
}

.rightTitle {
    font-weight: bold;
}

.type {
    word-spacing: 0.5rem;
    font-weight: bold;
}

.codeName {
    word-spacing: 0.5rem;
    margin-right: 0.1rem;
    position: absolute;
    line-height: 0.6rem;
    font-weight: bold;
}

.codeMean {
    margin-right: 0.1rem;
    position: absolute;
    line-height: 0.6rem;
    font-weight: bold;
}

.mccCode .yd-flexbox:nth-child(1) .yd-flexbox-item {
    margin-left: 1.05rem;
}

.mccCode .yd-flexbox:nth-child(2) .yd-flexbox-item {
    margin-left: 0.55rem;
}

.mccCode .yd-flexbox:nth-child(3) .yd-flexbox-item {
    margin-left: 0.6rem;
}

.mccCode .yd-flexbox:nth-child(4) .yd-flexbox-item {
    max-width: 6rem;
    line-height: 0.6rem;
    margin-left: 2rem;
}

.mccCode .yd-flexbox:nth-child(5) .yd-flexbox-item {
    max-width: 6rem;
    line-height: 0.6rem;
    margin-left: 0.33rem;
}

.mccCode .yd-flexbox:nth-child(6) .yd-flexbox-item {
    margin-left: 2rem;
}

.mccCode .yd-flexbox:nth-child(7) .yd-flexbox-item {
    margin-left: 1.95rem;
}

.noResults {
    font-size: 0.28rem;
    text-align: center;
}
</style>


